<template>
  <div>
    <van-tabs v-model="activeTab" @click="onClick" sticky :offset-top="45">
      <van-tab v-for="(os, index) in orderStatus" :key="index" :title="os.title">
        <div v-for="item in goods" :key="item.id" class="order-list-item">
          <van-cell title="订单号:1234567890" value="待支付"></van-cell>
          <van-card class="order-list-content" :title="item.title" centered :num="item.num"
                    :price="formatPoints(item.price)" :thumb="item.thumb">
            <div slot="footer">
              <van-button plain round type="danger" size="small">取消订单</van-button>
              <van-button plain round color="#1989fa" size="small">付款</van-button>
            </div>
          </van-card>
        </div>
      </van-tab>
    </van-tabs>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
    </van-list>
  </div>
</template>

<script>
import { Tab, Tabs, Card, List, Cell } from "vant";

export default {
  name: "OrderList",
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [List.name]: List,
    [Cell.name]: Cell,
    [Card.name]: Card
  },

  data() {
    return {
      activeTab: 0,
      loading: false,
      finished: false,
      orderStatus: [
        {
          status: "",
          title: "全部"
        },
        {
          status: "1",
          title: "待支付"
        },
        {
          status: "2",
          title: "已付款"
        }
      ],
      goods: [
        {
          status: "0",
          id: "1",
          title: "进口香蕉",
          desc: "约250g，2根",
          price: 200,
          num: 1,
          thumb:
            "https://img.yzcdn.cn/public_files/2017/10/24/2f9a36046449dafb8608e99990b3c205.jpeg"
        },
        {
          status: "1",
          id: "2",
          title: "陕西蜜梨",
          desc: "约600g",
          price: 690,
          num: 1,
          thumb:
            "https://img.yzcdn.cn/public_files/2017/10/24/f6aabd6ac5521195e01e8e89ee9fc63f.jpeg"
        },
        {
          status: "2",
          id: "3",
          title: "美国伽力果",
          desc: "约680g/3个",
          price: 2680,
          num: 1,
          thumb:
            "https://img.yzcdn.cn/public_files/2017/10/24/320454216bbe9e25c7651e1fa51b31fd.jpeg"
        },
        {
          status: "2",
          id: "4",
          title: "美国伽力果",
          desc: "约680g/3个",
          price: 2680,
          num: 1,
          thumb:
            "https://img.yzcdn.cn/public_files/2017/10/24/320454216bbe9e25c7651e1fa51b31fd.jpeg"
        }
      ]
    };
  },

  methods: {
    onClick(name, title) {
      this.$toast(title);
    },

    onLoad() {
      setTimeout(() => {
        this.goods = this.goods.concat(this.goods);
        this.loading = false;
        if (this.goods.length > 10) {
          this.finished = true;
        }
      }, 500);
    },

    formatPoints(points) {
      return points + "积分";
    }
  }
};
</script>

<style lang="less" scope="true">
.order-list {
  &-item {
    margin-top: 8px;
  }
  &-content {
    background-color: #fff;
    margin-top: 0px !important;
  }
}
</style>
